define('morgana/views/server-side-model-form-field', ['exports', 'ember'], function (exports, Ember) {

    'use strict';

    exports['default'] = Ember['default'].ContainerView.extend({
        model: null,

        init: function () {
            this._super();
            this.createChildViews();
        },


        createChildViews: function () {
            var ret,
                content,
                required = this.get('field.required'),
                model = this.get("model"),
                store = this.get('controller.store'),
                fieldName = this.get('field.fieldName'),
                fieldType = this.get('field.type'),
                readOnly = this.get('field.read_only'),
                placeholder = [this.get('field.help_text'), '[' + this.get('field.field_name') + ']'].join(' ');

            if (readOnly) {
                return;
            }
            switch (fieldType) {

            case 'url':
                ret = Ember['default'].TextField.create({
                    model: model,
                    valueBinding: 'model.' + fieldName,
                    type: 'url',
                    'data-tooltip': '',
                    required: required,
                    placeholder: placeholder,
                    title: placeholder,
                    classNames: ['has-tip'],
                    attributeBindings: ['data-tooltip']
                });
                break;
            case 'decimal':
            case 'integer':
                ret = Ember['default'].TextField.create({
                    model: model,
                    valueBinding: 'model.' + fieldName,
                    type: 'number',
                    'data-tooltip': '',
                    required: required,
                    placeholder: placeholder,
                    title: placeholder,
                    classNames: ['has-tip'],
                    attributeBindings: ['data-tooltip']
                });
                break;
            case 'boolean':
                ret = Ember['default'].Select.create({
                    model: model,
                    valueBinding: 'model.' + fieldName,
                    prompt: 'Unknown',
                    optionValuePath: "content.value",
                    optionLabelPath: "content.displayName",
                    content: [
                        {value: true, displayName: 'Yes'},
                        {value: false, displayName: 'No'}
                    ],
                    'data-tooltip': '',
                    required: required,
                    title: placeholder,
                    classNames: ['has-tip'],
                    attributeBindings: ['data-tooltip', 'title']
                });
                break;
            case 'choice':
                content = store.all(model.constructor.typeKey + 'Choice' + Ember['default'].String.capitalize(fieldName));
                ret = Ember['default'].Select.create({
                    model: model,
                    valueBinding: 'model.' + fieldName,
                    prompt: 'Unknown',
                    optionLabelPath: "content.displayName",
                    content: content,
                    'data-tooltip': '',
                    required: required,
                    title: placeholder,
                    classNames: ['has-tip'],
                    attributeBindings: ['data-tooltip', 'title']
                });
                break;
            case 'string':
                ret = Ember['default'].TextField.create({
                    model: model,
                    valueBinding: 'model.' + fieldName,
                    type: 'text',
                    'data-tooltip': '',
                    required: required,
                    placeholder: placeholder,
                    title: placeholder,
                    classNames: ['has-tip'],
                    attributeBindings: ['data-tooltip']
                });
                break;

            case 'json':
                ret = Ember['default'].TextArea.create({
                    model: model,
                    valueBinding: 'model.' + fieldName,
                    'data-tooltip': '',
                    required: required,
                    placeholder: placeholder,
                    title: placeholder,
                    classNames: ['has-tip'],
                    attributeBindings: ['data-tooltip']
                });
                break;
            default:
                ret = Ember['default'].TextField.create({
                    model: model,
                    valueBinding: 'model.' + fieldName,
                    type: 'text',
                    'data-tooltip': '',
                    required: required,
                    placeholder: placeholder,
                    title: placeholder,
                    classNames: ['has-tip'],
                    attributeBindings: ['data-tooltip']

                });
                break;
            }
            ret.reopen({

            });

            this.pushObject(ret);


        }
    });

});