define('morgana/routes/profile/edit-company', ['exports', 'ember'], function (exports, Ember) {

    'use strict';

    exports['default'] = Ember['default'].Route.extend({

        model: function (params) {
            var model = this.modelFor('profile');
            model.send('becomeDirty');
            model.rollback();
            return model;
        }
    });

});